.content {
    padding-top: 6.25rem;
    margin: auto;
    width: 16.875rem;
}

input {
    padding: 0 .625rem;
    width: 88%;
    height: 3.125rem;
    border-radius: 1.25rem;
    background: rgb(241, 241, 241);
    border: 0rem;
    margin-bottom: .625rem;
}

.key-content {
    margin-bottom: .625rem;
}

.btn {
    font-size: .9375rem;
    margin-right: 0.735rem;
    margin-top: .3125rem;
    text-align: center;
    width: 1.875rem;
    height: 1.875rem;
    line-height: 1.875rem;
    padding: .8rem;
    border-radius: 50%;
}

.btn-long {
    font-size: .9375rem;
    margin-right: 0.735rem;
    margin-top: .3125rem;
    text-align: center;
    width: 6.1rem;
    height: 1.875rem;
    line-height: 1.875rem;
    padding: .8rem;
    border-radius: 1.875rem;
}

.float-left {
    float: left;
}

.color-grey {
    background-color: #aeaeae;

}

.color-orange {
    color: #fff;
    background-color: #ff9900;
}

.color-black {
    color: #fff;
    background-color: #5f5f5f;
}

.animate {
    font-size: .9375rem;
    color: rgb(0, 162, 255);
    display: inline-block;
    white-space: nowrap;
    animation: 10s wordsLoop linear infinite normal;
}

@keyframes wordsLoop {
    0% {
        transform: translateX(0rem);
        -webkit-transform: translateX(0rem);
    }

    100% {
        transform: translateX(80rem);
        -webkit-transform: translateX(80rem);
    }
}

